.block-header {
	padding-top: 70px;
	padding-bottom: 150px;
	text-align: center;
}

.header-content {
	font-size: 40px;
	font-weight: 700;
	display: block;
	max-width: 870px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 50px;
}

.header-button {
	background: rgb(235,125,75);
	box-shadow: 0 -5px rgb(200,106,64) inset;
}

.header-button:hover { 
	background: rgb(200,106,64);
	box-shadow: 0 -5px rgb(200,106,64) inset;
	color: #fff;
}