.block-skills {
	text-align: center;
	padding-top: 100px;
	padding-bottom: 100px;
}

.skills-item-set {
	color: #3c4761;
}

.skills-item-circle {
	width: 160px;
	height: 160px;
	border: 15px solid #30bae7;
	border-radius: 50%;
	padding-top: 25px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 25px;
}

.circle-collor-2 {
	border: 15px solid #d74680;
}

.circle-collor-3 {
	border: 15px solid #15c7a8;
}

.circle-collor-4 {
	border: 15px solid #eb7d4b;
}

.skills-item-circle-text {
	font-size: 50px;
	color: #3c4761;
}

.skills-item-circle-text-tho {
	font-size: 35px;
}

.skills-item-content {
	text-transform: uppercase;
	font-size: 24px;
	font-weight: 700;
}

.skill-item {
	margin-top: 30px;
}

.skill-item-col {
	margin-top: 30px;
}