.block-team {
	text-align: center;
	padding-top: 100px;
	padding-bottom: 100px;
}

.team-item-set {
	color: #3c4761;
}

.team-ava {
	border-radius: 50%;
	background-color: #878787;
	height: 200px;
	width: 200px;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 15px;
}

.team-item-title {
	text-transform: uppercase;
	font-weight: 600;
	font-size: 24px;
	color: #27283d;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-top: 50px;
	margin-bottom: 25px;
}

.team-item-sup {
	color: #30bae7;	
}

.team-item-content {
	margin-top: 25px;
	color: #3c4761;
	display: block;	
	margin-left: auto;
	margin-right: auto;	
}

.team-item {
	margin-top: 30px;
}

.team-item-col {
	margin-top: 30px;
}