html, body {
	height: 100%;
}

.wrapper {
	min-height: 100%;
	width: 100%;
	overflow: hidden;
	padding-bottom: 68px;
}

.container {
	max-width: 1170px;
	padding-left: 15px;
	padding-right: 15px;
	margin-right: auto;
	margin-left: auto;
}

.screen-header {
	background-color: #87509c;
}

.screen-navigation {
	color: #fff;
}

.screen--services {
	background-color: #17c2a4;
}

.screen--team {
	background-color: #e7f1f8;
}

.screen--portfolio {
	background-color: #ffdd99;
}

.screen--testimonials{
	background-color: #d74680;
}

.screen--get-in-touch {
	background-color: #3c5499;
}

.screen--footer {
 	height: 68px;
 	margin-top: -68px;
	background-color: #344b8e;
}