.block-portfolio {
	text-align: center;
	padding-top: 100px;
	padding-bottom: 100px;

}

.portfolio-item-set {
	color: #000;
}

.portfolio-tab-row {
	margin-top: 30px;
	margin-bottom: 30px;
}

.portfolio-tab {
	background-color: #ebc985;
	height: 40px;
	border-radius: 10px;
	font-size: 18px;
	color: #393939;
	padding-top: 5px;
}

.portfolio-tab:hover {
	background-color: #fff;
}

.portfolio-tab.active {
	background-color: #fff;
}

.portfolio-screen {
	display: none;
}

.portfolio-screen.active {
	display: block;
}

.portfolio-screen-row {
	margin-top: 30px;
}

.portfolio-img {
	width: 100%;
}

.portgfolio-item-content {
	color: #393939;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-top: 20px;
	margin-bottom: 20px;
	font-weight: 700;
	font-size: 18px;
}

.portfolio-button {
	margin-top: 50px;
	display: block;
	max-width: 300px;
	margin-left: auto;
	margin-right: auto;
}