.button-item {
	font-weight: 600;
	font-size: 18px;
	color: white;
	text-decoration: none;
	text-transform: uppercase;
	padding: 20px 50px;
	border-radius: 3px;
	background: rgb(64,199,129);
	box-shadow: 0 -3px rgb(53,167,110) inset;
	transition: 0.2s;
}

.button-item:hover { 
	background: rgb(53, 167, 110); 
	text-decoration: none;
	color: #fff;
}

.button-item:active {
	background: rgb(33,147,90);
	box-shadow: 0 3px rgb(33,147,90) inset;
}

.item-title {
	font-size: 40px;
	color: #fff;
	text-transform: uppercase;
	font-weight: 600;
	letter-spacing: 6px;
}

.item-line {
	width: 86px;
	height: 4px;
	background-color: rgba(0,0,0,.2);
	margin-left: auto;
	margin-right: auto;
	margin-top: 20px;
	margin-bottom: 20px;
}

.item-content {
	display: block;
	color: #fff;
	font-size: 18px;
	max-width: 580px;
	margin-left: auto;
	margin-right: auto;
}