@media screen and (max-width: 910px) { 
	.input-get-in-touch {
		display: block;	
		width: 100%;
	}
}

.block-get-in-touch {
	padding-top: 100px;
	padding-bottom: 100px;
	text-align: center;
	color: #fff;
}

.input-get-in-touch {
	display: block;
	font-weight: 700;
	padding-left: 25px;
	background-color: #273a71;
	width: 100%;
	height: 50px;
	margin-bottom: 30px;
	margin-top: 30px;
	border: none;
	color: #fff;
	border-radius: 4px;

}

.textarea-get-in-touch {
	display: block;
	width: 100%;
	height: 100px;
	background-color: #273a71;
	color: #fff;
	border: none;
	border-radius: 4px;
	font-weight: 700;
	padding-left: 25px;	
}

.get-in-touch {
	max-width: 880px;
	margin-left: auto;
	margin-right: auto;
}

.button-get-in-touch {
	border: none;
	margin-top: 50px;
	background-color: #30bae7;
	box-shadow: 0 -3px #299ec5 inset;
}

.button-get-in-touch:hover { 
	background: #299ec5; 
	text-decoration: none;
	color: #fff;
}

.button-get-in-touch:active {
	background: #299ec5;
	box-shadow: 0 3px #299ec5 inset;
}
