@media screen and (max-width: 576px) {
	.block-testimonials {
		text-align: center;
	}
}

.block-testimonials {
	padding-top: 100px;
	padding-bottom: 100px;
}

.testimonials-item-set {
	color: #fff;
	text-align: center;
}

.testimonials-item-set  {
	color: #fff;
}
.testimonials-ava {
	width: 100px;
	height: 100px;
	margin-left: auto;
	margin-right: auto;
	border-radius: 50%;
	background-color: #787878;
}

.testimonials-content-italic {
	font-style: italic;
	font-size: 21px;
	color: #fff;
}

.testimonals-content-name {
	margin-top: 30px;
	display: block;
	font-size: 24px;
	color: #fff;
	font-weight: 700;
}

.testimonals-content-work {
	color: #fd9;
	font-size: 18px;
}

.row-margin {
	padding-top: 40px;
}

.col-margin {
	margin-top: 30px;
}